// 引入防抖
import {debounce} from '../../common/js/common.js'
// 引入定位
import {addressData} from '../../common/network/map.js'
// 引入SDK核心类
var QQMapWX = require('../../common/QQmap/qqmap-wx-jssdk.js');
var qqmapsdk;

export default{
	name:'Search',
	data(){
		return{
			address:'请定位',
			isSearch:false,
			city:[],
			keyAddress:''
		}
	},
	mounted(){
		this.location()
	},
	methods:{
		location(){
			addressData()
				.then(res => {
					this.address = res.result.ad_info.city
					console.log(res)
				})
				.catch(err => {
					console.log('用户拒绝定位')
				})
		},
		tapResult(city){
			this.myNavigateTo(city)
		},
		tapAddress(){
			let city = this.address
			this.myNavigateTo(city)
		},
		tapCity(item){
			this.myNavigateTo(item)
		},
		// 公共跳转函数
		myNavigateTo(city){
			// vuex传值city
			this.$store.commit('setCity',city)
			uni.navigateBack({
				delta:1
			})
		},
		onSearch(){
			console.log('search')
			this.isSearch = true
		},
		closeSearch(){
			this.isSearch = false
			this.keyAddress = ''
			this.city = []
		},
		// 实时搜索城市
		searchAddress(e){
			console.log(e.detail.value)
			let key = e.detail.value
			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'IOBBZ-KX4LI-ITAGZ-5SJDP-J43CE-DIFPX'
			});
			qqmapsdk.getSuggestion({
				keyword:key,
				fiter:'category=景区,美食',
				success:(res => {
					// this.city = res.data
					let city = res.data
					// 取出城市名
					let mainCity = res.data[0].city
					console.log(mainCity)
					// 过滤数据
					let citydata = city.map(item => {
						return item.title
					})
					console.log(citydata)
					// 第一个都是市级地区
					this.city = [mainCity,...citydata]
				}),
				fail:(err => {
					console.log(err)
				})
			})
		}
	}
}
